.container{
    text-align: center;
}
.title{
    margin-top: 50px;
    color: white;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.title span{
    color: #26ffcb;
    padding-left:15px;
}
.reset{
    width: 250px;
    height: 100px;
    border:none;
    outline: none;
    cursor:pointer;
    border-radius: 50px;
    background:#1f3540;
    font-size: 26px;
    color: #26ffcb;
    margin-top: 25px;
    margin-bottom: 50px;
}
.boxes{display: flex;height: 180px;width: 180px;background: #1f3540;border:4px solid #0f1b21;border-radius: 12px;cursor: pointer;}
.border{display: flex;height: 600px;width: 600px;margin: auto;}
.boxes img{margin:50px;}